import React from "react";
import "./home.css";
import { makeStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Data from "../Data/Data";
import Random from "./random/Random";
import mrnobody from "../posters/mrnobody.jpg";
function Home() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);
    const [isMobile, setIsMobile] = React.useState(true);
    const handleIsMobile = () => {
        if (window.innerWidth <= 540) setIsMobile(false);
        else setIsMobile(true);
    };

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    React.useEffect(() => {
        handleIsMobile();
    }, []);

    window.addEventListener("resize", handleIsMobile);
    return (
        <>
            <div className="root-container">
                <div className="app-container ">
                    <div className="week-movie-container">
                        <div className="title-wrapper">
                            <h1> + This Week Movie +</h1>
                            <h2>(Mr.Nobody)</h2>
                        </div>
                        <div className="week-movie-wrapper">
                            <img src={mrnobody} alt="" />
                        </div>
                    </div>
                    <div className="random-comp">
                        <Random />
                    </div>

                    <div className={classes.root}>
                        <Tabs
                            orientation="vertical"
                            variant="scrollable"
                            value={value}
                            onChange={handleChange}
                            aria-label="Vertical tabs example"
                            className={classes.tabs}
                        >
                            {Data.map((data) => {
                                return (
                                    <Tab
                                        key={data.id}
                                        label={` ${data.name} `}
                                        {...a11yProps(data.id)}
                                    />
                                );
                            })}
                        </Tabs>

                        {Data.map((data) => {
                            return (
                                <div key={data.id}>
                                    <TabPanel value={value} index={data.id}>
                                        {data.movies.map((e) => {
                                            return (
                                                <div
                                                    key={e.id}
                                                    className="movie-container"
                                                >
                                                    <div className="movie-name-wrapper">
                                                        {e.name}
                                                    </div>
                                                    {isMobile && (
                                                        <div className="movie-year-wrapper">
                                                            {` | ${e.year} |`}
                                                        </div>
                                                    )}

                                                    {isMobile && (
                                                        <div className="imdb-wrapper">
                                                            {e.imdb}
                                                        </div>
                                                    )}

                                                    <div
                                                        id="is-watched"
                                                        className={
                                                            e.isWatched
                                                                ? "watched"
                                                                : "not-watched"
                                                        }
                                                    >
                                                        {e.isWatched
                                                            ? "Watched"
                                                            : "Not-Watched"}
                                                    </div>
                                                    {isMobile && (
                                                        <div className="download-link">
                                                            <a
                                                                href={e.dlLink}
                                                                target="_blank"
                                                                rel="noreferrer"
                                                            >
                                                                Download
                                                            </a>
                                                        </div>
                                                    )}
                                                </div>
                                            );
                                        })}
                                    </TabPanel>
                                </div>
                            );
                        })}
                    </div>

                    <div className={classes.accordion}>
                        {Data.map((data) => {
                            return (
                                <div key={data.id}>
                                    {data.movies.map((e) => {
                                        if (e.review !== "") {
                                            return (
                                                <Accordion key={e.id}>
                                                    <AccordionSummary
                                                        expandIcon={
                                                            <ExpandMoreIcon />
                                                        }
                                                        aria-controls="panel1a-content"
                                                        id="panel1a-header"
                                                    >
                                                        <h1
                                                            className={
                                                                classes.heading
                                                            }
                                                        >
                                                            {e.name}
                                                        </h1>
                                                        </AccordionSummary>
                                                        <hr />
                                                    <AccordionDetails>
                                                        <Typography>
                                                            {e.review}
                                                        </Typography>
                                                    </AccordionDetails>
                                                </Accordion>
                                            );
                                        }
                                    })}
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </>
    );
}

export default Home;

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </div>
    );
}

function a11yProps(index) {
    return {
        id: `vertical-tab-${index}`,
        "aria-controls": `vertical-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: "#2d2d2d",
        display: "flex",
        height: "450px",
        width: "90%",
        borderRadius: "15px",
        padding: "80px 0",
        filter: "drop-shadow(2px 15px 15px rgb(17, 17, 17))",
        ["@media (max-width:970px)"]: {
            width: "95%",
        },
        ["@media (max-width:540px)"]: {
            width: "95%",
        },
        ["@media (max-width:376px)"]: {
            width: "95%",
        },
    },
    tabs: {
        borderRight: `2px solid ${theme.palette.divider}`,
    },

    accordion: {
        width: "90%",
        height: "10px",
        marginTop: "50px",
        marginBottom:"80px",
       
    },
    heading: {
        fontSize: theme.typography.pxToRem(20),
        fontWeight: theme.typography.fontWeightBold,
        textTransform: "capitalize",
    },
}));
